import React from 'react';
import styled from 'styled-components';
import Responsive from './Responsive';

const FooterBlock = styled.div`
  /* margin-top: auto; */
  width: 100%;
  background: #343a40;
`;

/* Responsive        */
const FooterWrapper = styled(Responsive)`
  height: 8rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .logo {
    color: white;
    font-size: 1.2rem;
    /* font-weight: bold; */
    font-family: 'Patua One', cursive;
    letter-spacing: 4px;
  }
  .compony {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #868e96;
    font-size: 0.85rem;
  }
`;

const Footer = () => {
  return (
    <>
      <FooterBlock>
        <FooterWrapper>
          <div className="logo">FIGTABLE</div>
          <div className="compony">
            <span>() </span>
            <span>  14 6, 4 ()</span>
            <span> 2018 FigTable , Ltd. All rights reserved.</span>
          </div>
        </FooterWrapper>
      </FooterBlock>
    </>
  );
};

export default Footer;
